import { Component, OnInit } from '@angular/core'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { CompanyDepartemenService, CompanyDivisiService } from 'src/app/sdk/hr/services'
import { NgxSpinnerService } from 'ngx-spinner'
import { Router } from '@angular/router'
import { Departemen, Divisi } from 'src/app/sdk/hr/models'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss'],
})
export class AddComponent implements OnInit {
    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyDivisiService: CompanyDivisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) {}

    data: Divisi = {}
    listDepartemen: Departemen[] = []
    dataForm: FormGroupTypeSafe<Divisi>
    ngOnInit() {
        this.form()
        this.getPreeData()
    }

    getPreeData() {
        this.companyDepartemenService.getCompanyDepartemens(JSON.stringify({status: 'active'})).subscribe(data => {
            this.listDepartemen = data.data
        })
    }

    form() {
        this.dataForm = this.fb.group<Divisi>({
            nama_divisi: new FormControl('', [Validators.required]),
            id_departemen: new FormControl('', [Validators.required]),
            kode_divisi: new FormControl('', [Validators.required]),
            status: new FormControl('Active', [Validators.required]),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.companyDivisiService.postCompanyDivisis(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/hr/company/module/divisi/list'])
    }
}
