import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'

import { DivisiRoutingModule } from './divisi-routing.module'
import { ListComponent } from './list/list.component'
import { EditComponent } from './edit/edit.component'
import { AddComponent } from './add/add.component'
import { FormsModule, ReactiveFormsModule } from '@angular/forms'
import { NgZorroAntdModule } from 'ng-zorro-antd'
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper'
import { PipeModule } from 'src/app/pipe/pipe.module'

@NgModule({
    declarations: [ListComponent, EditComponent, AddComponent],
    imports: [
        CommonModule,
        DivisiRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        PipeModule
    ],
    providers: [FormBuilderTypeSafe],
})
export class DivisiModule {}
